.lzr-background {
  position: relative;
  background: url('/heroimage.png');
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}
.lzr-background::before {
  content: "";
  position: absolute;
  inset: 0;
  background-image: repeating-linear-gradient(
      to right,
      rgba(224,236,236,0.06) 0px,
      rgba(224,236,236,0.06) 1px,
      transparent 1px,
      transparent 24px
    ),
    repeating-linear-gradient(
      to bottom,
      rgba(224,236,236,0.06) 0px,
      rgba(224,236,236,0.06) 1px,
      transparent 1px,
      transparent 24px
    );
  opacity: .15;
  mask-image: radial-gradient(1200px 600px at 50% 20%, black 40%, transparent 80%);
  pointer-events: none;
  will-change: transform;
  transform: translate3d(calc(var(--px, 0px) * .45), calc(var(--py, 0px) * .45), 0);
  transition: transform 160ms ease-out;
}
.lzr-background::after {
  content: "";
  position: absolute;
  inset: 0;
  background: repeating-linear-gradient(
    90deg,
    transparent 0 50px,
    rgba(30,58,71,0.10) 50px 52px
  );
  mix-blend-mode: screen;
  opacity: .08;
  animation: shimmer 12s linear infinite;
  pointer-events: none;
  will-change: transform;
  transform: translate3d(calc(var(--px, 0px) * .75), calc(var(--py, 0px) * .75), 0);
  transition: transform 200ms ease-out;
}
@keyframes shimmer {
  from {
    transform: translateX(0);
  }
  to {
    transform: translateX(50px);
  }
}
.nav-compact {
